
.ui.menu {
  margin: 0;
  border-radius: 0px;
}
.ui.menu .item.router-link-active, .ui.menu .item.router-link-active:hover {
  background: rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.95);
  font-weight: 400;
  box-shadow: none;
}
.ui.segment.container {
  margin-top: 0px;
  padding-top: 50px;
  min-height: calc(100vh - 42px);
  border-radius: 0px;
}



